@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --bg-dark: #1E2326;
  --primary-white: #ffffff;
  --primary-blue: #1A7BB7;
  --primary-blue-hover: #1A6FA3;
}

body {
  font-family:"Roboto Condesed", sans-serif;
}

/* ----------------------------------------------- */
/* Navbar */
.navbar {
  background-color: #1E2326 !important;
  background-color: var(--bg-dark) !important;
}

.navbar-brand {
  width: 16.25rem;
  font-size:xx-large;
  max-width: 17,25rem;
  pading: 0 !important;
}
.navbar-brand > a {
  color: #1A7BB7 !important;
  color: var(--primary-blue) !important;
}

.navbar-toggler:hover {
  border: none !important;
}
.navbar-toggler:focus {
  border: none !important;
}
/* Menu Link */
.nav a.nav-link {
  font-size: .8rem;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #ffffff !important;
  color: var(--primary-white) !important;
}

.navbar-nav > li > a.active, .navbar-nav > li > a:hover {
  border-bottom: 0.1875rem solid #1A7BB7;
  border-bottom: 0.1875rem solid var(--primary-blue);
  color: #1A7BB7 !important;
  color: var(--primary-blue) !important;
}


/* ----------------------------------------------- */
/* Header */
.header-wraper {
  position: relative;
  background-color: #1E2326;
  background-color: var(--bg-dark);
  background-size: cover;
  background-position: center;
  height: 90vh;
}

.main-info {
  display:flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family:"Roboto Condesed", sans-serif;
}
.main-info h1 {
  color: #ffffff;
  color: var(--primary-white);
  text-transform: uppercase;
  padding: 1rem;
  border: 1px solid #ffffff;
  border: 1px solid var(--primary-white);
}

/* Typed Text */
.typed-text {
  font-size: 2rem;
  color: #ffffff;
  color: var(--primary-white);
}

/* Header main offer button */
.btn-main-offer {
  text-decoration: none;
  padding: 0.5rem;
  color: #ffffff;
  color: var(--primary-white);
  background-color: #1A7BB7;
  background-color: var(--primary-blue);
}
.btn-main-offer:hover {
  color: #ffffff;
  color: var(--primary-white);
  background-color: #1A6FA3;
  background-color: var(--primary-blue-hover);
}
